import React from "react";
import ReactDOM from "react-dom/client";
import reportWebVitals from "./reportWebVitals";
import TimeAgo from "javascript-time-ago";

// @ts-ignore
import en from "javascript-time-ago/locale/en";
// @ts-ignore
import { Timeline, Bookmark } from "react-vertical-timeline";
import "react-vertical-timeline/style.css";

import { Col, Row } from "react-flexbox-grid";

TimeAgo.addDefaultLocale(en);

const dataBonn: Event[] = [
  {
    label: `Find out about the University`,

    date: Date.parse(`2023-03-20`),
  },
  {
    label: `Permanent residency application`,

    date: Date.parse(`2023-06-1`),

    requirements: [{ name: "German B2 Certificate" }],
  },
  {
    label: `Hattersheimer contract end`,

    date: Date.parse(`2024-07-1`),

    requirements: [{ name: "German B2 Certificate" }],
  },
  {
    label: `----- German course application`,

    date: Date.parse(`2024-05-01`),
  },
  {
    label: `----- Dorm application`,

    date: Date.parse(`2024-02-15`),
  },
  {
    label: `University application`,

    date: Date.parse(`2024-03-15`),
  },

  {
    label: `University Start`,

    date: Date.parse(`2024-10-10`),
  },
];

interface Requirement {
  name: string;
  accomplished?: boolean;
}

interface Event {
  percent?: number;

  label: string;
  description?: string;
  date: number;
  requirements?: Requirement[];
}

// const EventCard: React.FC<Event> = (props) => <></>;
const EventLabel: React.FC<Event> = (props) => (
  <>
    {props.label} - {formatDate(props.date)} ({timeDifference(props.date)})
  </>
);

const App = () => {
  const { now, data } = processEvents(dataBonn);
  return (
    <Row center="xs" middle="xs" style={{ height: "100vh" }}>
      <Col xs md={6}>
        <Timeline height={window.innerHeight * 0.8} progress={now}>
          {data.map((e) => (
            <Bookmark progress={e.percent} key={e.date}>
              <EventLabel {...e} />
            </Bookmark>
          ))}
        </Timeline>
      </Col>
    </Row>
  );
};

function processEvents(data: Event[]) {
  if (data.length < 2) return { now: 0, data: [] };

  const oneMonth = 30 * 24 * 60 * 60 * 1000;

  const start = data[0].date - oneMonth;
  const end = data[data.length - 1].date + oneMonth;

  const percent = (n: number) => {
    const p = ((n - start) / (end - start)) * 100;
    return p > 100 ? 100 : Math.round(p);
  };

  return {
    now: percent(Date.now()),
    data: data.map((e) => ({ ...e, percent: percent(e.date) })),
  };
}

function formatDate(d: number): string {
  return new Intl.DateTimeFormat("en-US", { dateStyle: "full" }).format(d);
}

function timeDifference(d: number): string {
  return new TimeAgo("en-US").format(d);
}

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
